.m-autocomplete {
  position: relative;
}
.m-autocomplete input {
  width: 100%;
}

.m-autocomplete__results {
  box-sizing: border-box;
  width: 100%;
  max-height: 200px;
  border: 1px solid var(--pacific);
  border-top-width: 0;
  padding-left: 0;
  overflow-y: scroll;
  position: absolute;
  z-index: 1000;
  background-color: white;
  list-style-type: none;
}
.m-autocomplete__results li {
  border-bottom: 1px solid var(--gray-20);
  margin-bottom: 0;
}
.m-autocomplete__results li:hover, .m-autocomplete__results li.m-autocomplete__selected {
  background-color: var(--gray-5);
}
.m-autocomplete__results li:last-child {
  border-bottom-width: 0;
}
.m-autocomplete__results a {
  display: block;
  padding: 0.46875em;
  border-width: 0;
  color: var(--black);
}

